import { Component, OnInit } from '@angular/core';
import { GenreService } from 'src/app/services/genre.service';
import { EditeurService } from 'src/app/services/editeur.service';
import { DeveloppeurService } from 'src/app/services/developpeur.service';
import { PlateformeService } from 'src/app/services/plateforme.service';
import { JeuVideo } from 'src/app/services/interfaces/jeu-video';
import { NavController } from '@ionic/angular';
import { DataJeuVideoService } from 'src/app/services/data-jeu-video.service';
import { AuthService } from 'src/app/services/auth.service';

@Component({
	selector: 'app-ajouter-jeu-video',
	templateUrl: './ajouter-jeu-video.page.html',
	styleUrls: [ './ajouter-jeu-video.page.scss' ]
})
export class AjouterJeuVideoPage implements OnInit {
	public genre: string[];

	public genreSelected1: string = null;
	public genreSelected2: string = null;

	public oldGenreSelected1: string = null;
	public oldGenreSelected2: string = null;
	public editeur: string[];
	public developpeur: string[];
	public plateformes: string[];
	public genreDispo1: string[];
	public genreDispo2: string[];

	public jeuVideoEntered: JeuVideo = {
		dateS: null,
		developpeur: null,
		editeur: null,
		genre1: null,
		genre2: null,
		id: null,
		nom: null,
		plateformes: null,
		commentaire: null,
		imageURL: null,
		note: 2,
		userID: 'slr3073'
	};

	constructor(
		genreService: GenreService,
		editeurService: EditeurService,
		developpeurService: DeveloppeurService,
		plateformeService: PlateformeService,
		private JeuVideoService: DataJeuVideoService,
		private navCtrl: NavController,
		private authService: AuthService
	) {
		this.genre = [ ...genreService.genre ];
		this.genreDispo1 = [ ...this.genre ];
		this.genreDispo2 = [ ...this.genre ];
		this.editeur = [ ...editeurService.editeur ];
		this.developpeur = [ ...developpeurService.developpeur ];
		this.plateformes = [ ...plateformeService.plateformes ];
	}

	ngOnInit() {}

	goBack() {
		this.navCtrl.navigateBack([ '/selectionner-jeu-video' ]);
	}

	onChange1() {
		//changement sur le select 1
		if (this.oldGenreSelected1 !== null) {
			this.genreDispo2.push(this.oldGenreSelected1);
		}
		this.genreDispo2.splice(this.genreDispo2.findIndex((e) => e == this.jeuVideoEntered.genre1), 1);
		this.genreDispo2 = this.genreDispo2.sort();
		this.oldGenreSelected1 = this.jeuVideoEntered.genre1;
	}

	onChange2() {
		//changement sur le select 2
		if (this.oldGenreSelected2 !== null) {
			this.genreDispo1.push(this.oldGenreSelected2);
		}
		this.genreDispo1.splice(this.genreDispo1.findIndex((e) => e == this.jeuVideoEntered.genre2), 1);
		this.genreDispo1 = this.genreDispo1.sort();
		this.oldGenreSelected2 = this.jeuVideoEntered.genre2;
	}

	Ajouter() {
		//traitements avant insertion
		this.jeuVideoEntered.nom = this.jeuVideoEntered.nom.toUpperCase();
		this.jeuVideoEntered.dateS = +this.jeuVideoEntered.dateS.toString().substring(0, 4);

		this.JeuVideoService.addInAllJeuVideo(this.jeuVideoEntered, this.authService.userId);
		this.navCtrl.navigateBack([ '/medias/jeux-video' ]);
	}

	passerNote(val: number) {
		this.jeuVideoEntered.note = val;
	}
}
